<template>

  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <screen title="" />
      <actionButtons :columns="actionbtn" @customClick="customClick" />
      <el-dialog title="" :visible.sync="btndialogadd" width="30%" :show-close="false">
        <el-form :model="btnItemData" label-width="120px">
          <!-- label: '', // 
        btnPlace: '', //   1  2
        btnType: '', //    [textbtn]svg[svgIcon]icon[icon]
        name: "", //        
        btnFlag: "", //   
        remark: "", //  -->
          <el-form-item label="">
            <el-select v-model="btnItemData.btnPlace" placeholder="" class="el-dialog_row_input" @change="btnPlaceChange">
              <el-option label="" :value="1" />
              <el-option label="" :value="2" />
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="btnItemData.label" :disabled="btnItemData.btnPlace===1" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="btnItemData.btnType" placeholder="" class="el-dialog_row_input" :disabled="btnItemData.btnPlace===1" @change="$forceUpdate()">
              <el-option label="" value="textbtn" />
              <el-option label="svg" value="svgIcon" />
              <el-option label="icon" value="icon" />
            </el-select>
          </el-form-item>
          <el-form-item :label="btnItemData.btnType==='textbtn'?'':''">
            <el-input v-model="btnItemData.name" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item v-if="btnItemData.btnPlace===1" label="">
            <el-input v-model="btnItemData.operateIcon" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="btnItemData.operatorProperties" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="btnItemData.remark" autocomplete="off" class="el-dialog_row_input" />
          </el-form-item>
        </el-form>
        <div style="text-align:center">
          <el-button type="primary" @click="subbtn"></el-button>
          <el-button @click="btndialogadd=false"></el-button>
        </div>
      </el-dialog>
      <el-table :data="tableData1" :span-method="objectSpanMethod" border style="width: 100%; margin-top: 20px">
        <el-table-column prop="label" label="" align="center" width="100" show-overflow-tooltip />
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.btnPlace===1?'':'' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.btnType==='textbtn'?'': scope.row.btnType==='svgIcon'?'svg': 'icon' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="{row}">
            <div>  <i v-if="row.operateIcon" :class="['icon', row.operateIcon.indexOf('el-icon')!==-1? row.operateIcon: ['iconfont' , row.operateIcon ? row.operateIcon :'']]" /> {{ row.name }} </div>
          </template>
        </el-table-column>
        <el-table-column prop="btnFlag" align="center" label="" show-overflow-tooltip />
        <el-table-column label="" align="center">
          <template>
            <span></span>
          </template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template>
            <span></span>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="" show-overflow-tooltip />
        <!-- <el-table-column prop="remark" label="" show-overflow-tooltip /> -->
        <el-table-column label="" width="150" align="center">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="operationBtn(scope.row,'edit')"></el-button>
            <el-button type="text" size="small" @click="operationBtn(scope.row.id,'del')"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import { getBtnIndex } from "@/api/functionPage.js";

import { sorts, AcquireMerge } from "@/assets/js/onsolidatedTable.js";

export default {
  components: {
    actionButtons: () => import("@/components/BaseCom/actionButtons"),
    screen: () => import("@/components/BaseCom/screen")
  },
  filters: {
    filterRouteType(item) {
      switch (item) {
        case 0:
          return "";
        case 1:
          return "";
        default:
          return "";
      }
    },
  },
  data() {
    return {
      actionbtn: [
        { label: "", icon: "icon-add", type: 'add', },
        { label: "", icon: "icon-del", type: 'del', },
      ],
      //  btnPlace    1  2       2  1
      //  tableHeader      
      //  btnType   [textbtn]svg[svgIcon]icon[icon]
      //  name       
      //  operateIcon     
      //  btnFlag  
      // 

      btnItemData1: {
        label: '', // 
        btnPlace: '', //   1  2
        btnType: '', //    [textbtn]svg[svgIcon]icon[icon]
        name: "", //        
        btnFlag: "", //   
        operateIcon: "", // 
        remark: "", // 
      },
      btnItemData: {
        label: ''
      },
      tableData1: [],

      addType: "",
      tableData: [],
      dialogTableVisible: false,
      btndialog: false,
      btndialogadd: false,
      btnData: [],
      pageId: "",
      spanArr: []
    }
  },
  mounted() {
    this.view()
  },
  methods: {
    async view() {
      const result = await getBtnIndex()
      this.tableData = result.data
      this.tableData1 = sorts(this.tableData, 'label')// value
      this.spanArr = AcquireMerge(this.tableData1)// 
    },
    btnPlaceChange(value) { // 
      if (value === 1) {
        this.btnItemData.label = ""
        this.btnItemData.btnType = 'textbtn'
      } else {
        this.btnItemData.label = ""
        this.btnItemData.btnType = ""
      }
    },
    async operationBtn(row, type) {

    },
    customClick() {
      this.btndialogadd = true
    },
    async subbtn() {
      // const result = await createBtn(this.btnItemData, this.btnItemData.id ? 'put' : 'post')
      // console.log(result)
    },
    // 
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) { // 
        if (this.spanArr[rowIndex]) {
          return {
            rowspan: this.spanArr[rowIndex],
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
  },

}
</script>
<style lang="scss" scoped>
.el-dialog_row_input {
  width: calc(100% - 120px);
}
.table-sty {
  height: 100%;
  ::v-deep .el-table__body-wrapper {
    height: calc(100% - 60px) !important;
    overflow-y: auto;
  }
  ::v-deep ::-webkit-scrollbar {
    /**/
    width: 3px;
    height: 8px;
  }
  ::v-deep ::-webkit-scrollbar-thumb {
    /**/
    border-radius: 5px;
    // -webkit-box-shadow: inset 0 0 5px rgba(1, 21, 48, 1);
    // box-shadow: inset 0 0 5px rgba(1, 21, 48, 1);
    background: #c1c8ce;
  }
  ::v-deep ::-webkit-scrollbar-track {
    /**/
    // -webkit-box-shadow: inset 0 0 5px rgba(14, 54, 94, 0.2);
    // box-shadow: inset 0 0 5px rgba(14, 54, 94, 0.2);
    border-radius: 5px;
    background: #fff;
  }
}
</style>
