import request from '@/utils/request'

export function getBtnIndex(data) { // 
  return request({
    url: '/functionPage/menu/btnlist',
    method: 'post',
    data
  })
}
export function getMenuIndex(data) { // 
  return request({
    url: '/functionPage/menu/index',
    method: 'post',
    data
  })
}
export function getRoleDetail(data) { // 
  return request({
    url: '/functionPage/role/detail',
    method: 'post',
    data
  })
}

