<template>
  <div>
    <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" />
      <el-table-column label="" width="120">
        <template slot-scope="scope">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column prop="name" label="" width="120" />
      <el-table-column prop="address" label="" show-overflow-tooltip />
    </el-table>
    <div style="margin-top: 20px">
      <el-button @click="toggleSelection([tableData[1], tableData[2]])"></el-button>
      <el-button @click="toggleSelection()"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [{
        date: '2016-05-03',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-08',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-06',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-07',
        name: '',
        address: ' 1518 '
      }],
      multipleSelection: []
    }
  },

  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
  }
}
</script>
