<template>
  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <tip-dialog v-model="reason" title="" dialog-type="input" tool-tips="" :btn-param="btnParam" :visible.sync="inputVisible" @affirm="affirm" />
      <el-button @click="openDialog(1)"></el-button>
      <tip-dialog v-model="reason" title="" dialog-type="tips" tool-tips="" :btn-param="btnParam" :visible.sync="tipVisible" @affirm="affirm" />
      <el-button @click="openDialog(2)"></el-button>
      <batch-preview title="" :visible.sync="batchPreviewVisible" :file-list="fileList" />
      <el-button @click="openDialog(3)"></el-button>
      <!-- <el-button @click="openDialog(2)">docx</el-button>
      <el-button @click="openDialog(3)">doc</el-button>
      <el-button @click="openDialog(4)"></el-button> -->
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "AReplenishment",
  components: {
    batchPreview: () => import('@/components/BaseCom/batchPreview/index'),
    tipDialog: () => import("@/components/BaseCom/tipDialog/index"),
  },
  data() {
    return {
      batchPreviewVisible: false,
      inputVisible: false,
      tipVisible: false, // 
      reason: "", // 
      btnParam: [{ btnlable: '', type: 'danger' }, { btnlable: '', type: 'success' }], // 
      fileList: [],
    };
  },
  mounted() {
  },
  methods: {
    openDialog(index) {
      if (index === 1) {
        this.inputVisible = true
      } else if (index === 2) {
        this.tipVisible = true
      } else if (index === 3) {
        this.batchPreviewVisible = true
        this.fileList = [
          { name: 'pdf', url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/0ff58062-0f82-476f-9d66-52d9e9bd7361.pdf' },
          { name: 'doc', url: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-4ad7ad0d-671d-4e62-b708-7519aabab27d/607241ca-3083-4105-8776-8f34eee61e6a.doc' },
          { name: '', url: 'https://itree.i-ortho.cn/api/upload/20220705/_20220704151224.jpg' }
        ]
      }
      // this.tipVisible = true;
    },
    affirm(type, value) {
      console.log(type, value)
    },
  }
};
</script>
