<template>
  <el-container class="app-container">
    <el-main style="background:white;padding: 0px 20px;">
      <screen title="" />
      <el-button @click="unrepeated"></el-button>
      <el-button @click="filterData('isAcceptStatus')"></el-button>
      <el-button @click="recursionFunction"></el-button>
      <el-button @click="createMockData">mock</el-button>
      <el-button @click="saveJsonFile">json</el-button>
      <el-button @click="antiShake"></el-button>
      <el-button @click="throttle"></el-button>
      <screen title="" />
      <el-button v-debounce="[reset,`click`,1000]"></el-button>
      <el-input v-model="value" v-debounce="[reset,`input`,1000]" placeholder="" />
      <el-button v-btnDebounce:500 @click="reset"></el-button>
      <el-button v-btnThrottle @click="reset"></el-button>
      <el-input v-model="value" v-inputDebounce:500="reset" placeholder="" />
      : <span v-relativeTime="1659329744959">1</span>
      <!-- <div>
        <el-button @click="getUserInfo"></el-button>
        <el-button @click="getUserInfo2">2</el-button>
      </div> -->
    </el-main>
  </el-container>
</template>
<script>
import { unrepeated, recursionFunction, createMockData, antiShake, saveMobility, saveJsonFile, filterData } from "@/assets/js/common";
import { goods } from "./mockStatic";
export default {
  name: "AReplenishment",
  components: {
    screen: () => import("@/components/BaseCom/screen/index.vue")
  },
  data() {
    return {
      value: ""
    };
  },
  mounted() {

  },
  methods: {
    filterData(fun) { // fun [{label: '', value: 1}]
      console.log(filterData(fun))
    },
    reset() {
      console.log("reset");
    },
    unrepeated() { // 
      const oldarr = [
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
        { id: 4, name: '' },
        { id: 1, name: '' },
        { id: 5, name: '' },
        { id: 3, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
      ]
      const newarr = unrepeated(oldarr, 'id')// oldarr , 'id '
      console.log(newarr)// 
    },

    recursionFunction() { // 
      const oldarr = [{
        id: 1,
        name: "",
        child: [{ id: 2, name: "" }, { id: 3, name: "" }]
      }]
      // oldarr
      // this.childRecursion (),
      // child children
      const newData = recursionFunction(oldarr, this.childRecursion, "child")
      console.log(newData)// 
