      console.log(goods)
      const oldobj = {
        id: "id",
        name: '',
        phone: "",
        email: "",
        goodsName: goods.name, // 
        specs: goods.specs, // 
        model: goods.model, // 
        address: "",
        date: "",
        time: "",
        datetime: "",
        color: "",
        number: "",
        price: "",
        type: "",
        status: ",1,5", //  1 5  5  1
        image: "",
        file: "",
        cparagraph: "",
        csentence: "",
        ctitle: ""
      }
      //       const aa = {
      //         id: "id",
      //       goodsName: goods.name, // 
      //         specs: goods.specs, // 
      //         model: goods.model, // 
      //         createDate: "",
      //         image: "",
      //  status: ",1", //  1 5  5  1
      //       }
      const newobj = createMockData(oldobj)// oldobj ,key value
      // const newobj = createMockData(aa)// oldobj ,key value
      console.log(newobj)// mock
      this.saveJsonFile(newobj, 'mockData.json')// newobj,mockData.json
    },
    saveJsonFile(obj, fileName) { // obj,fileName
      saveJsonFile(obj, fileName)
    }

  }
};
</script>
